/* CSS for disappearing navbar */

.ScrollingUp {
  background-color: #242424;
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  top: 0;
  z-index: 999;
  transition: top 0.2s ease-in-out;
}

.ScrollingDown {
  background-color: #242424;
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  top: -80px;
  z-index: 999;
  transition: top 0.2s ease-in-out;
}
