.cards {
    padding: 4rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  
  .cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    margin: 0 auto;
    width: 100%;
  }
  
  .cards__wrapper {
    position: relative;
    margin: 50px 0 45px;
    width: 100%;
  }
  
  .cards__items {
    margin-bottom: 24px;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    cursor: default;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }

  .facebook{
    display: flex;
    justify-content: center; 
  }
  .facebook-post{
    width: 100%;
    max-width: 550px;
    overflow: visible !important;
  }

  .our-teachers{
    display: flex;
    justify-content: center;
    gap: 5rem;
  }

  .teachers{
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    max-width: 100%;
    width: 30rem;
  }

  .teacher{
    width: 80%;
    filter: grayscale(1);
    cursor: pointer;
  }
  
  .name-plate{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: center;
    width: 100%;
    height: 35%;
    position: absolute;
    bottom: 0;
    background-color: white;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    padding: 30px 20px;
  }

  .full-name{
    font-size: 1.8rem; 
  }

  .description{
    font-size: 1rem;
  }

  .social-icons{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }

  .teacher:hover{
    transition: all 0.3s ease 0s;
    filter: grayscale(0);
  }
  
  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .our-teachers{
      flex-direction: column;
      align-items: center;
    }
    .cards__item {
      margin-bottom: 2rem;
    }
    .cards{
      padding: 2rem;
    }
  }
